<template>
  <div class="Me">
    <div class="MO_kuang" v-show="weixin">
      <span style="font-family:'幼圆';font-weight:blod;font-size: 25px;" @click="xx()" class="xx">X</span>
      <div style="position: relative;">
        <img class="alldown_img" src="./../assets/images/down.jpg" alt />
        <div class="downlogo_zi">微信扫一扫下载APP</div>
      </div>
    </div>
    <div class="head_top">
      <div class="top_logo">
        <a href>
          <img class="down" src="./../assets/images/logo2.png" alt />
        </a>
      </div>
      <div class="top_li">
        <ul>
          <li @click="navx()">首页</li>
          <li @click="navx()">企服者</li>
          <li @click="navx()">发布</li>
          <li @click="navx()">消息</li>
          <li @click="navx()">我的</li>
        </ul>
      </div>
      <div class="sou">
        <img class="soulogo" @click="navx()" src="./../assets/images/soulogo.png" alt />
        <input class="sousuo" type="text" />
        <img class="sou_biao" src="./../assets/images/biao.png" alt />
        <span class="allDown" @click="dow()">下载易传APP</span>
      </div>
      <div></div>
      <div class="logon">
        <!-- <span>登陆</span>|<span>注册</span> -->
      </div>
    </div>
    <div id="banner">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in bannerlist" :key="index">
            <img @click="navx()" :src="item.img" />
          </div>
        </div>

        <div class="swiper-pagination"></div>
      </div>
    </div>
    <div style="width: 100%;">
      <div class="dongtai">
        <ul style="list-style: none;">
          <li>
            <span style="float: left;">动态列表</span>
            <span style="float: right;">查看全部></span>
          </li>
        </ul>
        <div style="margin-top: 40px;display: flex;flex-wrap: wrap;">
          <div class="liebiao" v-for="(item,index) in rows" :key="index">
            <div style="width:40%;">
              <img class="biao_img" @click="navx()" :src="item.imageList" alt />
            </div>
            <div class="biao_zi ziti">
              <h4 @click="navx()">{{item.title}}</h4>
              <p
                class="xq_zi"
                style="margin-top: -5px;"
                v-if="item.releaseTypeId==1"
              >经营范围:{{item.business}}</p>
              <p
                class="xq_zi"
                style="margin-top: -5px;"
                v-if="item.releaseTypeId==3"
              >详细内容:{{item.detail}}</p>
              <p class="xq_zi">
                <img
                  style="float: left;height: 15px;width: 15px;"
                  src="./../assets/images/hei8.png"
                />
                {{item.cityName}}
                <span style="float: right;">{{item.releaseTime}}</span>
              </p>
              <p class="xq_zi">
                <span style="color: red">￥{{item.price}}</span>
                <span style="float: right;">浏览{{item.browse}}次</span>
              </p>
            </div>
          </div>
        </div>
        <div class="Y_ma">
          <div @click="navx()">上一页</div>
          <div @click="navx()">当前是第5页</div>
          <div @click="navx()">下一页</div>
          <!-- <div>共170条</div> -->
          <div @click="navx()">最后一页</div>
          <div @click="navx()">第一页</div>
        </div>
      </div>
      <div class="center_right">
        <div>
          <img
            style="width: 100%;border-top-left-radius: 5px;height: 30%;border-top-right-radius: 5px;"
            src="./../assets/images/center_right_top1.png"
            alt
          />
        </div>
        <div class="box">
          <h4>投资人（企服者）认证流程投资人认证</h4>
          <input type="text" style="margin-top: -15px;" id="name" placeholder="请输入投资人姓名" />
          <input type="text" id="company" placeholder="请输入公司名称" />
          <input type="text" id="mobile" placeholder="请输入手机号" />
          <button class="tou_btn" @click="btn()">提交</button>
        </div>
      </div>
      <div class="foot">
        <p>
          <span>联系电话：</span>
          <span style="margin-right:5px;">010-53681018</span>|
          <span style="margin-left: 10px;">联系地址：北京市丰台区南四环西路188号</span>
        </p>
        <p>
          <img style="position: relative;top: 5px;left: -2px;" src="./../assets/images/must.jpg" />京公网安备11010602006911号
          <span>&nbsp;邮箱：yz_vip_admin@163.com</span>
        </p>
        <p>易转(北京)互联网信息科技有限公司 版权所有 © 2018-2019 yz-vip.cn, All Rights Reserved.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Me",
  data() {
    return {
      weixin: false,
      mssg: "",
      content: "",
      bannerlist: [
        {
          img: require("./../assets/images/sw1.jpg")
        },
        {
          img: require("./../assets/images/sw2.jpg")
        },
        {
          img: require("./../assets/images/sw3.jpg")
        },
        {
          img: require("./../assets/images/sw4.jpg")
        },
        {
          img: require("./../assets/images/sw5.jpg")
        },
        {
          img: require("./../assets/images/sw6.jpg")
        },
        {
          img: require("./../assets/images/sw6.png")
        }
      ],
      rows: []
      // showText:false
    };
  },
  created() {
    let param = {
      cityId: 0,
      page: 1,
      size: 10
    };
    this.$axios
      .get("https://app.yz-vip.cn/webUser/dynamic", param)
      .then(response => {
        // this.content =response
        // console.log("ok");
        if (response.data.msg == "请求成功") {
          this.rows = response.data.data.rows;
          this.rows.forEach(function(index, i) {
            let ins = index.price.slice(0, index.price.indexOf("."));
            let a = ins.split("0").length - 1;
            if (a == 0) {
              index.price = ins.slice(0, index.price.indexOf("."));
            } else if (a > 4) {
              // console.log(ins.slice(-index.price.length, -4));
              index.price = ins.slice(-index.price.length, -4) + "万";
            } else if (a == 4) {
              index.price = ins.slice(-index.price.length, -4) + "万";
            } else if (a == 7) {
              index.price =
                ins.slice(-index.price.length, -8) +
                "亿" +
                ins.slice(-index.price.length, -4) +
                "万";
            } else {
              index.price = ins.slice(0, index.price.indexOf("."));
            }
          });
        } else {
          alert(response.data.msg);
        }
      })
      .catch(function(error) {
        console.log(error);
      });
  },

  mounted: function() {
    new window.Swiper(".swiper-container", {
      //   direction:    'vertical', // 垂直切换选项
      loop: true, // 循环模式选项,
      effect: "coverflow",
      slidesPerView: 3,
      centeredSlides: true,
      keyboard: true,
      grabCursor: true,
      mousewheel: true,
      autoplay: {
        disableOnInteraction: false,
        delay: 2000
      },

      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination"
      }
    });
  },
  methods: {
    dow: function(e, em) {
      console.log(e);
      this.weixin = true;
    },
    xx() {
      this.weixin = false;
    },
    navx() {
      alert("敬请期待,如有需求请下载：<易传App>");
      this.Navs = false;
    },
    btn() {
      let name = document.querySelector("#name").value;
      let campany = document.querySelector("#company").value;
      let mobile = document.querySelector("#mobile  ").value;
      let params = {
        name: name,
        company: campany,
        mobile: mobile
      };
      var myreg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
      if (!myreg.test(mobile)) {
        alert("您输入的信息有误，请核对后再次提交！");
      } else {
        if (name == "" || campany == "") {
          alert("请完善您的信息！");
        } else {
          console.log("手机号格式正确");
          console.log(name, "公司名字：" + campany, "手机号：" + mobile);
      //     let tokens =
      // "eyJhbGciOiJIUzI1NiJ9..dmdr0wmtpm58vwXrnJ1t94P4hy7hjU0r5tHdFm5QabA";
    
          let params = {
            name: name,
            company: campany,
            mobile: mobile
          };
          // this.axios({
          //   method: "get",
          //   url:
          //     "http://101.200.122.133:8183/webUser/saveWebUser" +
          //     qs.strimgify(params),
          //   headers: {
          //     token: tokens
          //   }
          // }).then(res => {
          //   console.log(res);
          //   console.log("成功");
          // });
          this.$axios
            .post("http://101.200.122.133:8183/webUser/saveWebUser", params)
            .then(res => {
              console.log("ok");
              alert(
                "信息提交成功，一到两个工作日内会有专门的负责人会与您取得联系！！"
              );
            });
        }
      }
    }
  }
};
</script>

<style scoped>
.swiper-container {
  clear: both;
  width: 100%;
  height: 300px;
}

.swiper-slide {
  height: 100%;
  width: 100%;
  border-radius: 10px;
}

.swiper-slide img {
  height: 100%;
  border-radius: 5px;
}

.swiper-container-3d {
  perspective: 1480px;
}

.Me {
  background: #f0f0f0;
  padding: 12px;
  margin: -10px;
}
.head_top {
  width: 100%;
  background: #fff;
  height: 60px;
  box-shadow: 0px -2px 0px 0px #edebeb;
  box-shadow: 0px 2px 0px 0px #edebeb;
  margin-bottom: 5px;
}

.top_li {
  float: left;
}

.top_logo {
  float: left;
}

.top_li > ul {
  list-style: none;
}

.top_li li {
  float: left;
  margin-left: 100px;
  line-height: 30px;
}

.top_li li:hover {
  color: red;
  cursor: pointer;
}

.sou {
  float: right;
  position: relative;
  margin-right: 100px;
  padding-top: 15px;
}

.sousuo {
  background: #f0f0f0;
  height: 28px;
  width: 200px;
  border: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  outline: none;
  float: left;
}

.soulogo {
  width: 40px;
  height: 30px;
  float: left;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.sou_biao {
  width: 30px;
  height: 30px;
  float: left;
}

.logon {
  float: right;
  margin-right: 20px;
  margin-top: 20px;
}

.dongtai {
  width: 70%;
  padding-top: 10px;
  padding-right: 3%;
  background: #fff;
  margin-top: 20px;
  border-radius: 5px;
  float: left;
}

.liebiao {
  width: 45%;
  height: 150px;
  box-shadow: 0px 2px 0px 0px #edebeb;
  margin-left: 40px;
  display: flex;
  margin-top: 30px;
}
.biao_zi {
  width: 55%;
  padding-left: 10px;
  height: 90%;
  padding-bottom: 5px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.biao_zi h4 {
  margin-top: 0px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.biao_img {
  width: 100%;
  margin-left: 2px;
  border-radius: 5px;
}

.xq_zi {
  font-size: 13px;
  color: #7e7d7d;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.MO_kuang {
  width: 70%;
  height: 70%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 15%;
  left: 15%;
  z-index: 10;
  border-radius: 20px;
}

.allDown {
  line-height: 28px;
  cursor: pointer;
}

.xx {
  cursor: pointer;
  color: #f0f0f0;
  font-style: oblique;
  transform: rotate(-14deg);
  position: absolute;
  right: 30px;
  top: 20px;
}
.alldown_img {
  border-radius: 20px;
  position: fixed;
  top: 25%;
  left: 44%;
  width: 13%;
}

.center_right {
  width: 25%;
  top: 10px;
  left: 10px;
  float: left;
  margin-top: 20px;
  background-size: 100%;
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 10px;
}
.box {
  background: url("./../assets/images/center_right_bottom2.png") no-repeat;
  top: 0px;
  left: 0px;
  background-size: 100%;
  padding-top: 10%;
  padding-left: 10%;
  margin-top: -5px;
  padding-bottom: 45%;
  z-index: 2;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.box input {
  outline: none;
  width: 70%;
  height: 30px;
  border: none;
  box-shadow: 0px 2px 0px 0px #edebeb;
  margin-top: 10px;
  border-radius: 2px;
}
.tou_btn {
  width: 80%;
  height: 30px;
  margin-top: 20px;
  border-radius: 2px;
  border: none;
  outline: none;
  cursor: pointer;
}
#Code {
  position: absolute;
  top: 10px;
  left: 10px;
}
.foot {
  clear: both;
  height: 120px;
  width: 100%;
  margin-top: 10px;
  text-align: center;
  background: #fff;
  padding-bottom: 10px;
  padding-top: 10px;
  border-radius: 5px;
  color: #4d4d4d;
  font-size: 13px;
}

.downlogo_zi {
  position: fixed;
  top: 60%;
  left: 45%;
  color: #fff;
  font-size: 20px;
}
.Y_ma {
  display: flex;
  height: 50px;
  line-height: 50px;
  margin-top: 49px;
  margin-bottom: 4%;
}
.Y_ma div {
  margin-left: 5%;
  width: 140px;
  height: 50px;
  border-radius: 5px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: cornflowerblue;
}
.ziti {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>



