<template>
  <div class="h5">
    <div class="box">
      <div class="head">
        <div class="logo">
          <img style="width: 100%" src="./../assets/images/logo2.png" />
        </div>
        <div class="navs">
          <img
            att="false"
            @click="navs()"
            width="60%"
            style="margin-top: .4rem;margin-left: .2rem;"
            src="./../assets/images/nav.png"
          />
        </div>  
        <div class="login">
          <!-- <img style="width: 18%;float: left;margin-top: .5rem;" src="images/sousuo2.png" alt=""> -->
          <div
            class="down"
            v-on:click="down"
            style="float: right !important;line-height: 2.7rem;margin-right: 1rem;font-size: .8rem"
          >点击下载易传App</div>
        </div>
      </div>
      <div style="height: 3rem;"></div>
      <div id="banner">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item,index) in bannerlist" :key="index">
              <img @click="navx()" :src="item.img" />
            </div>
          </div>

          <div class="swiper-pagination"></div>
        </div>
      </div>
      <div class="center">
        <ul style="list-style: none;">
          <li style="height: 3rem;line-height: 3rem;">
            <h5>
              <span style="float: left;margin-left: 2%;">动态列表</span>
              <span @click="navx()"
                style="float: right;font-size: .7rem;margin-right:2%;color: #898787;"
                class="lists"
              >查看全部 》</span>
            </h5>
          </li>
        </ul>
        <div class="list" style="margin-top:-2rem;">
          <div class="biao" v-for="(item,index) in rows" :key="index">
            <!-- <span>{{item}}</span> -->
            <div class="bieo_img">
              <img style="float: left" @click="navx()" :src="item.imageList" alt />
              <div class="biao_cent">
                <h5 style="margin-top: .2rem;" @click="navx()">{{item.title}}</h5>
                <p class="ziti" style="font-size: .7rem;margin-top: -1rem;" v-if="item.releaseTypeId==1">经营范围:{{item.business}}</p>
                <p class="ziti" style="font-size: .7rem;margin-top: -1rem;" v-if="item.releaseTypeId==3">详细内容:{{item.detail}}</p>
                <p style="font-size: .7rem;margin-top: -1rem;">
                  <img
                    style="width: 8%;position: relative;top: .2rem;left:-.4rem;"
                    src="./../assets/images/hei8.png"
                    alt
                  />
                  <span style="line-height: 2rem;margin-left:-.5rem;">{{item.cityName}}</span>
                  <span style="line-height: 2.5rem;float: right;">{{item.releaseTime}}</span>
                </p>
                <p style="position: relative;top:-1rem;">
                  <span style="font-size: .9rem;color: red;float: left;">￥{{item.price}}</span>
                  <span style="float: right;font-size: .8rem;color: #635c5c;">浏览{{item.browse}}次</span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="nav" v-show="Navs">
      <ul>
        <li @click="navx()">首页</li>
        <li @click="navx()">企服者</li>
        <li @click="navx()">发布</li>
        <li @click="navx()">消息</li>
        <li @click="navx()">我的</li>
      </ul>
    </div>
    <div class="foot">
      <p>
        <span>联系电话：</span>
        <span style="margin-right:5px;">010-53681018</span>|
        <span style="margin-left: 10px;">联系地址：北京市丰台区南四环西路188号</span>
      </p>
      <p>
        <img style="position: relative;top: 5px;left: -2px;" src="./../assets/images/must.jpg" />京公网安备11010602006911号
        <span>&nbsp;邮箱：yz_vip_admin@163.com</span>
      </p>
      <p>易转(北京)互联网信息科技有限公司 版权所有 © 2018-2019 yz-vip.cn, All Rights Reserved.</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "H5",
  data() {
    return {
      Navs: false,
      mssg: "",
      content: "",
      bannerlist: [
        {
          img: require("./../assets/images/sw1.jpg")
        },
        {
          img: require("./../assets/images/sw2.jpg")
        },
        {
          img: require("./../assets/images/sw3.jpg")
        },
        {
          img: require("./../assets/images/sw4.jpg")
        },
        {
          img: require("./../assets/images/sw5.jpg")
        },
        {
          img: require("./../assets/images/sw6.jpg")
        },
        {
          img: require("./../assets/images/sw6.png")
        }
      ],
      rows: []
      // showText:false
    };
  },
  created() {
    let param = {
      cityId: 0,
      page: 1,
      size: 10
    };
    this.$axios
      .post("https://app.yz-vip.cn/webUser/dynamic", param)
      .then(response => {
        // this.content =response
        console.log("ok");
        if (response.data.msg == "请求成功") {
          this.rows = response.data.data.rows;
          this.rows.forEach(function(index, i) {
            let ins = index.price.slice(0,index.price.indexOf('.'))
            let a = (ins.split('0')).length-1;
            if(a==0){
            index.price = ins.slice(0,index.price.indexOf('.'))
            }
            else if(a>4){
            console.log(ins.slice(-index.price.length,-4))
            index.price = ins.slice(-index.price.length,-4)+"万"
            }
            else if(a==4){
            index.price = ins.slice(-index.price.length,-4)+"万"
            }
            else if(a==7){
            index.price = ins.slice(-index.price.length,-8)+"亿"+ins.slice(-index.price.length,-4)+"万"
            }
            else{
            index.price = ins.slice(0,index.price.indexOf('.'));
            }
          });
        } else {
          alert(response.data.msg);
        }
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  mounted: function() {
    //配置Swiper  建议关于Swiper配置方面可以前往Swiper官网
    new window.Swiper(".swiper-container", {
      effect: "fade",
      loop: true, // 循环模式选项
      speed: 2000,
      autoplay: {
        disableOnInteraction: false,
        delay: 2500
      },
      // 如果需要分页器
      pagination: {
        el: ".swiper-pagination"
      }
    });
  },
  methods: {
    down() {
      window.location.href = "https://yz-vip.cn/download/default.html";
    },
    navs: function(e, em) {
      if (this.Navs == false) {
        document.querySelector(".nav").setAttribute("style", "display:block;");
        this.Navs = true;
      } else if (this.Navs == true) {
        this.Navs = false;
        document.querySelector(".nav").setAttribute("style", "display:none;");
      }
    },
    navx() {
      alert("敬请期待,如有需求请下载：<易传App>");
      this.Navs = false;
    }
  }
};
</script>

<style scoped>
.H5{
  background: #dad8d8;
}
* {
  padding: 0;
}

body {
  background: #f0f0f0;
  margin: 0;
}

.head {
  width: 100%;
  height: 6vh;
  background: #fff;
  position: fixed;
  z-index: 10;
  margin-left: -0.2rem;
  margin-top: -0.5rem;
}

.logo {
  float: left;
  width: 20%;
  margin: 0.2rem;
}

.navs {
  float: left;
  cursor: pointer;
}

.login {
  float: right;
}

.swiper-container {
  width: 100%;
}
.swiper-slide img {
  width: 100%;
  border-radius: 5px;
}

.swiper-container-3d {
  perspective: 1480px;
}

.center {
  width: 100%;
  background: #fff;
  margin-top: 1rem;
}

.biao {
  width: 100%;
  box-shadow: 0px 2px 0px 0px #dad8d8;
  float: left;
  background: #fff;
  margin-top: 0.1rem;
  padding-bottom: 0.2rem;
  padding-top: 0.3rem;
}

.bieo_img img {
  width: 35%;
  border-radius: 0.3rem;
  margin-left: 0.3rem;
  margin-top: 0.4rem;
}

.foot {
  clear: both;
  /* height: 120px; */
  width: 100%;
  margin-top: 1rem;
  text-align: center;
  background: #fff;
  padding-bottom: 1rem;
  padding-top: 1rem;
  /* border-radius: 5rem; */
  color: #4d4d4d;
  font-size: 0.4rem;
}
.biao_cent {
  width: 54%;
  float: left;
  margin-left: 1rem;
  padding-right: 0.3rem;
}
.nav {
  position: fixed;
  top: 1.5rem;
  left: 0rem;
  z-index: 11;
}
.nav ul {
  list-style: none;
  text-align: center;
}
.nav li {
  width: 10rem;
  height: 3rem;
  background: #0ca7ff;
  color: #fff;
  line-height: 3rem;
  /* background: #fff; */
  /* margin-bottom: .2rem; */
  /* border-bottom:.1rem solid #635c5c; */
  margin-bottom: 0.3rem;
  border-radius: 0.2rem;
}
.nav li:hover {
  background: #72ccffc7;
  /* color: red; */
}
.nav a {
  text-decoration: none;
}
.ziti {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>



