import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
import H5 from "../components/H5.vue"
import Me from "../components/Me.vue"

export default new Router({
  routes: [
    {
      path: "*",
      component: Me
    },
    {
      path: '/Me',
      name: 'Me', //对路由起别名
      component: Me
    }, {
      path: "/H5",
      name: 'H5', //对路由起别名
      component: H5
    }

  ]
})



